.Footer{
    display: flex;
    border-bottom: 1px solid var(--border-bar-color);
    align-items: center;
    width: 100%;
    font-size: .75rem;
    transition: all .5s ease;
  }

.Footer > .PaginationArrowsContainer svg {
    display: block;
}  

/* Media queries */
@media screen and (min-width: 600px){
  .Footer{
    font-size: .94rem;
    height: 2.44rem;
  }       
}

@media screen and (min-width: 800px){
  .Footer{
    font-size: 1.13rem;
    height: 3.17rem;
  } 
}

@media screen and (min-width: 1000px){
  .Footer{
    font-size: 1.32rem;
    height: 3.89rem;    
  } 
}

@media screen and (min-width: 1200px){
  .Footer{
    font-size: 1.5rem;
    height: 4.62rem;
  }    
}