@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
/* Fonts */

*{
  box-sizing: border-box;
  margin:0;
  padding:0;
  font-family: 'Source Sans Pro', sans-serif;
}

:root{
  --light-font-color: rgb(255, 255, 255);
  --medium-font-color: rgb(163, 163, 163); 
  --dark-font-color: rgb(97, 97, 97);

  --main-bkgd-color: rgb(242, 242, 242);
  --secondary-bkgd-color: rgb(237, 237, 237);
  --border-bar-color: rgb(217, 217, 217);
}

body {
  background-color: rgb(242, 242, 242);
  background-color: var(--main-bkgd-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App{
  overflow: hidden;
}
.Header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 3rem;
    padding: .875rem 1rem;
    transition: height .5s ease;
}

.Header__LogoAndLangWrapper{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-grow: 1;
  grid-gap:4%;
  gap:4%;
}

.Header__Logo{
  width: 1.25rem;
  transition: width .5s ease;
}

.Header__LanguageWrapper{
  display: flex;
  align-items: center;
  grid-gap: .16rem;
  gap: .16rem;
  border: none;
  border-radius: 2rem;
  padding: 2% 2.5%;
  cursor: pointer;
  background-color: var(--secondary-bkgd-color);
  transition: all .5s ease;
}

.Header__LanguageWrapper:active, .Header__LanguageWrapper:active > *{
  -webkit-transform: scale(.98);
          transform: scale(.98);
}

.Header__LanguageLabel{
  font-size: .63rem;
  font-family: inherit;
  color: var(--dark-font-color);
}

.Header__LanguageFlag{
    width: .63rem;
    height: auto;
}

.Header__BannerImage{
    display: flex;
    position: relative;
    background: url(/reactrewardsstore/static/media/header-banner.b6f067c6.png);
    background-size: 100% 100%;
    height: 33.33vw;
    background-repeat: no-repeat;
    align-items: flex-end;
    margin-bottom: -.5rem;
    z-index: -2;
}

.Banner-Mask {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 85.35%, 0 85.35%);
            clip-path: polygon(0 0, 100% 0, 100% 85.35%, 0 85.35%);
    transition: -webkit-clip-path .4s linear;
    transition: clip-path .4s linear;
    transition: clip-path .4s linear, -webkit-clip-path .4s linear;
}

.Header__BannerText{
  position: absolute;
  bottom: 22%;
  left: 4.68%;
  font-weight: 700;
  color: var(--light-font-color);
  font-size: .875rem;
  transition: all .5s ease;
}

/* Media queries */
@media screen and (min-width: 600px){
    .Header {
      height: 3.25rem;
      padding: .98rem 1.4rem;
    }

    .Header__Logo{
      width: 1.75rem;
    }

    .Header__LanguageWrapper{
      grid-gap: .21rem;
      gap: .21rem;
      padding: .7% 1.5%;
    }

    .Header__LanguageLabel{
      font-size: .84rem;
    }

    .Header__LanguageFlag{
      width: .84rem;  
    }
    
    .Header__BannerText{
      bottom: 22.49%;
      left: 5.8%;
      font-size: 1.65rem;
    }  
}

@media screen and (min-width: 800px){
