.UserInfo{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: .63rem;
    gap: .63rem;
    color: var(--dark-font-color);
    font-size: .75rem;
    position: relative;
}

.UserInfo__PointsWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--secondary-bkgd-color);
    padding: .31rem .41rem;
    border-radius: 1rem;
    grid-gap: .16rem;
    gap: .16rem;
    transition: all .5s ease;
    cursor: pointer;
}

.UserInfo__Coin{
    width: .63rem;
    height: auto;
}

.User__InfoMenu{
    display: flex;
    justify-content: center;
    align-items: center;
}

.UserInfo__Arrow path{
    fill: var(--dark-font-color) !important;
}

.UserInfo__Arrow{
    width: .94rem;
    height: auto;
    padding-top: 3%;    
}

#UserInfo__CollapsibleCheck{
    opacity: 0;
    display: none;
}

.UserInfo__DropdownWrapper{
    width: 100%;
    list-style: none;
    position: absolute;
    top: 115%;
    left: 110vw;
    -webkit-transform: translateX(120%);
            transform: translateX(120%);
    margin: 0;
    background-color: white;
    padding-top: 3%;
    border-radius: 0 0 3px 3px;
    transition: -webkit-transform .4s linear;
    transition: transform .4s linear;
    transition: transform .4s linear, -webkit-transform .4s linear;
}

.UserInfo__DropdownOption{
    width: 100%;
    padding: 3% 10%;
}

.UserInfo__DropdownOption:last-child{
    border-radius: 0 0 3px 3px;
  }

.UserInfo__CollapsibleCheck:checked ~ .UserInfo__DropdownWrapper{
    -webkit-transform: translateX(0);
            transform: translateX(0);
    top: 115%;
    left: 0;
}

.UserInfo__ArrowWrapper{
    -webkit-transform: translateY(15%) translateX(10%);
            transform: translateY(15%) translateX(10%);
    transition: all .25s;
    cursor: pointer;
}

.UserInfo__CollapsibleCheck:checked + .UserInfo__ArrowWrapper{
    -webkit-transform: rotate(-90deg) translateY(15%);
            transform: rotate(-90deg) translateY(15%);
}

.MorePoints__OptionWrapper{
    display: flex;
    flex-direction: column;
    list-style: none;
    width: 85%;
    transition: all .3s ease-in;
    background-color: white;
    grid-gap: .75rem;
    gap: .75rem;
    padding: 5% 0;
}

.MorePoints__Option{
    background: rgb(97, 97, 97, .8);
    mix-blend-mode: normal;
    border-radius: 100px;
    color: var(--light-font-color);
    padding: .25rem .38rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: .75rem;
    grid-gap: .16rem;
    gap: .16rem;
    cursor: pointer;
}

