#spinner{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.1rem;
  }
  
  #spinner::after {
    content: "";
    width: 1.88rem;
    height: 1.88rem;
    border: 2px solid var(--medium-font-color);
    border-top: 3px solid rgb(10, 212, 250);
    border-radius: 100%;
    will-change: transform;
    -webkit-animation: spin 1s infinite linear;
            animation: spin 1s infinite linear
  }
  
  /* Animation */
  @-webkit-keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  /* Media Queries */
  @media screen and (min-width: 600px){
      #spinner{
          margin-top: 1.48rem;
      }
  
      #spinner::after {
        width: 2.66rem;
        height: 2.66rem;
      }
  }
  
  @media screen and (min-width: 800px){
      #spinner{
        margin-top: 1.86rem;
      }
  
      #spinner::after {
        width: 3.44rem;
        height: 3.44rem;
      }
  }
  
  @media screen and (min-width: 1000px){
      #spinner{
        margin-top: 2.24rem;
      }
  
      #spinner::after {
        width: 4.22rem;
        height: 4.22rem;
      }
  }
  
  @media screen and (min-width: 1400px){
      #spinner{
          margin-top: 2.63rem 0 3.75rem;
      }
  
      #spinner::after {
        width: 5rem;
        height: 5rem;
      }
  }
